

























html {
  height: 100%;
  cursor: nw-resize;
}

body {
  -webkit-box-align: center;
      -ms-flex-align: center;
              -ms-grid-row-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  background: url(../static/img/bg.jpg);
}

#app {
  color: #2c3e50;
  max-width: 600px;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  text-align: center;
  background: #fff;
  margin: 30px auto;
}

#app a {
  color: #53af31;
  text-decoration: none;
}
h1{
  margin-bottom: 1.5em;
}
.logo {
  max-width: 100%;
  max-height: 100%;
}
.inline{
  display: inline;
  max-height: 35px;
  vertical-align: middle;
}
ul{
  text-align: left;
}
li{
  list-style: none;
  margin-top: 1em;
}
li:before{
  content: ' ';
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url(../static/img/boule.jpg);
  background-size: cover;
  vertical-align: middle;
}



